.appear,
.enter {
  opacity: 0;
  transform: scale(0.9);
}

.appearActive,
.enterActive {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms, transform 300ms;
}

.exit {
  opacity: 1;
  transform: scale(1);
}

.exitActive {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}
